const swiper = new Swiper('#swiper', {
    // Optional parameters
    direction: 'horizontal',
    loop: true,
    slidesPerView: 4,
    speed: 5000,
    // Navigation arrows
    navigation: {
      nextEl: '.swiper-button-next',
      prevEl: '.swiper-button-prev',
    },
    autoplay: {
      delay: 0,
      enabled: true,
    },
    breakpoints: {
      // настройки для разных разрешений
      0: {
          slidesPerView: 'auto',
          spaceBetween: 8
        },
      500: {
          slidesPerView: 2,
          spaceBetween: 30
        },
      700: {
        slidesPerView: 3,
        spaceBetween: 30
        },
      1192: {
          slidesPerView: 4,
          spaceBetween: 20
      }
  }
  });
  