const headers = document.querySelectorAll('[data-tab]'),
      text = document.querySelectorAll('[data-name="text"]'),
      headers1 = document.querySelectorAll('[data-tab-1]'),
      text1 = document.querySelectorAll('[data-name="text-1"]');

headers.forEach(function (item)  {
    item.addEventListener('click', function ()  {

            text.forEach(function(item){
            item.classList.toggle('hidden');
        });
        
        const contentBox = document.querySelector('#' + this.dataset.tab);
        contentBox.classList.toggle('hidden');

    });
});
headers1.forEach(function (item)  {
    item.addEventListener('click', function ()  {

            text1.forEach(function(item){
            item.classList.toggle('hidden');
        });
        
        const contentBox = document.querySelector('#' + this.dataset.tab);
        contentBox.classList.toggle('hidden');

    });
});