const animateItems = document.getElementsByClassName('anim__content');

if(animateItems.length > 0){
    window.addEventListener('scroll', animOnScroll);
    function animOnScroll(params) {
        for( let index = 0; index < animateItems.length; index++){
            const animateItem = animateItems[index];
            const animateItemHeight = animateItem.offsetHeight;
            const animateItemOffset = offset(animateItem).top;
            const animateStart = 4;

            let animateItemPoint = window.innerHeight - animateItemHeight / animateStart;
            if(animateItemHeight > window.innerHeight){
                animateItemPoint = window.innerHeight - window.innerHeight / animateStart;
            }

            if ((scrollY > animateItemOffset - animateItemPoint) && scrollY < (animateItemOffset + animateItemHeight)) {
                animateItem.classList.add('animate');
            } else{
                //Возможность, чтобы анимация рпоходила тольок один раз
                if(!animateItem.classList.contains('second__anim-content')){
                //Возможность, чтобы анимация рпоходила только один раз
                    animateItem.classList.remove('animate');
                }
            }
        }
    }
    function offset(el) {
        const rect = el.getBoundingClientRect(),
            scrollLeft = window.pageXOffset || document.documentElement.scrollLeft,
            scrollTop = window.pageYOffset || document.documentElement.scrollTop;
        return {top:rect.top + scrollTop, left:rect.left + scrollLeft};
    }


    // Задержка анимации
                    // setTimeout(() => {
                    //     animOnScroll();
                    // }, 300);
    // Задержка анимации


    //Чтобы проходила анимация сразу же, надо вызвать функцию внутри этой же функции
    animOnScroll();
    //Чтобы проходила анимация сразу же, надо вызвать функцию внутри этой же функции
}